<template>
  <div class="dsf-player" @click="defaultClick()">
    <slot name="baberrage"></slot>
    <!-- <video-player
      class="video-player vjs-custom-skin"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
      @play="playerPlay($event)"
      @pause="playerPause($event)"
      @ended="playerEnded($event)"
      @loadeddata="playerLoadeddata($event)"
      @waiting="playerWaiting($event)"
      @playing="playerPlaying($event)"
      @timeupdate="playerTimeupdate($event)"
      @canplay="playerCanplay($event)"
      @canplaythrough="playerCanplaythrough($event)"
      @ready="playerReadied"
      @statechanged="playerStateChanged($event)"
    ></video-player> -->
  </div>
</template>
<script>
// import "videojs-contrib-hls";
// import "video.js/dist/video-js.css";
// import { videoPlayer } from "vue-video-player";
// import { state } from "../../../project/anHui/assets/style.scss";
export default {
  props: {
    autoPlay: {
      type: Boolean,
      default: false,
    },
    //
    isLive: {
      type: Boolean,
      default: false,
    },
    //
    netWork: {
      type: Boolean,
      default: false,
    },
    isPause: {
      //
      type: Boolean,
      default: true,
    },
    seekTime: {
      //
      type: Number,
      default: 0,
    },
    coverImg: {
      //
      type: String,
      default: "",
    },
    videoSource: {
      //
      type: [String],
    },
    //
    allowFastForward: {
      type: Boolean,
      default: false,
    },
    //
    playIng: {
      type: Boolean,
      default: false,
    },
    showMark: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      initPlay: false,
      isSeeking: false,
      playerOptions: {
        // isFullscreen:false,
        playbackRates: [0.7, 1.0, 1.5, 2.0], //
        autoplay: this.autoPlay, //true,
        muted: false, // 
        loop: false, // 
        preload: "auto", // <video>auto,
        language: "zh-CN",
        aspectRatio: "16:9", //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        hls: true,
        sources: [],
        poster: this.coverImg, //
        width: document.documentElement.clientWidth, //
        notSupportedMessage: "",
        languages: {
          "zh-CN": {
            Play: "",
            Pause: "",
            "Current Time": "",
            Duration: "",
            "Remaining Time": "",
            "Stream Type": "",
            LIVE: "",
            Loaded: "",
            Progress: "",
            Fullscreen: "",
            "Non-Fullscreen": "",
            Mute: "",
            Unmute: "",
            "Playback Rate": "",
            Subtitles: "",
            "subtitles off": "",
            Captions: "",
            "captions off": "",
            Chapters: "",
            "Close Modal Dialog": "",
            Descriptions: "",
            "descriptions off": "",
            "Audio Track": "",
            "You aborted the media playback": "",
            "A network error caused the media download to fail part-way.": "",
            "The media could not be loaded, either because the server or network failed or because the format is not supported.":
              "",
            "The media playback was aborted due to a corruption problem or because the media used features your browser did not support.":
              "",
            "No compatible source was found for this media.": "",
            "The media is encrypted and we do not have the keys to decrypt it.": "",
            "Play Video": "",
            Close: "",
            "Modal Window": "",
            "This is a modal window": "",
            "This modal can be closed by pressing the Escape key or activating the close button.": "ESC",
            ", opens captions settings dialog": ", ",
            ", opens subtitles settings dialog": ", ",
            ", opens descriptions settings dialog": ", ",
            ", selected": ", ",
            "captions settings": "",
            "Audio Player": "",
            "Video Player": "",
            Replay: "",
            "Progress Bar": "",
            "Volume Level": "",
            "subtitles settings": "",
            "descriptions settings": "",
            Text: "",
            White: "",
            Black: "",
            Red: "",
            Green: "",
            Blue: "",
            Yellow: "",
            Magenta: "",
            Cyan: "",
            Background: "",
            Window: "",
            Transparent: "",
            "Semi-Transparent": "",
            Opaque: "",
            "Font Size": "",
            "Text Edge Style": "",
            None: "",
            Raised: "",
            Depressed: "",
            Uniform: "",
            Dropshadow: "",
            "Font Family": "",
            "Proportional Sans-Serif": "",
            "Monospace Sans-Serif": "",
            "Proportional Serif": "",
            "Monospace Serif": "",
            Casual: "",
            Script: "",
            "Small Caps": "",
            Reset: "",
            "restore all settings to the default values": "",
            Done: "",
            "Caption Settings Dialog": "",
            "Beginning of dialog window. Escape will cancel and close the window.": "",
            "End of dialog window.": "",
          },
        },
      },
      playingTime: 0,
      timeOutTimer: null,
    };
  },
  created() {
    if (!this.isLive) {
      this.playerOptions.controlBar = {
        timeDivider: true,
        durationDisplay: true,
        remainingTimeDisplay: false,
        fullscreenToggle: true, //
      };
      this.$nextTick(() => {
        const nextBtn = this.player.controlBar.addChild("button");
        const waterMark = this.player.controlBar.addChild("button");
        //   menuBtn.addClass("vjs-icon-menu");
        //   menuBtn.on("tap", this.playerList);
        nextBtn.addClass("vjs-icon-next");
        nextBtn.on("tap", this.playerNext);
        // waterMark.addClass('waterMark');
      });
    }
    document.addEventListener("fullscreenchange", function (event) {
      if (document.fullscreenElement) {
        console.log("");
      } else {
        console.log("");
      }
    });
    this.playerOptions.sources = [this.videoSrc()]; // [this.videoSource];
  },
  mounted() {
    this.initPlay = true;
    var video = this.$el.querySelector("video");
    video.setAttribute("x5-video-player-type", "h5-page");
    video.setAttribute("x-webkit-airplay", true);
    video.setAttribute("x5-video-player-fullscreen", false);
    // alert(video.parentNode.innerHTML);
    video.removeAttribute("x5-playsinline");
    // video.setAttribute("x5-video-orientation","landscape|portrait")
    // video.setAttribute("raw-controls", false);
    //---
    // if (!this.isAndroid() && this.netWork) {
    //   this.playerLoadeddata()

    //   // this.player.play();
    // }
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    },
    videoSrc() {
      return function () {
        let url = this.videoSource;
        if (!url) return {};
        let arr = url.split(".");
        let extName = arr[arr.length - 1];
        let type = "";
        switch (extName) {
          // case "mp3":
          //   type = 'audio/mpeg3';
          //   break;
          case "m3u8":
            type = "application/x-mpegURL";
            break;
          case "mp4":
            type = "video/mp4";
            break;
          default:
            type = "";
            break;
        }
        if (type) {
          return {
            type: type,
            src: url,
          };
        } else {
          return url;
        }
      };
    },
  },
  // components: { videoPlayer },
  methods: {
    defaultClick() {
      console.log(1);
    },
    isAndroid() {
      let u = window.navigator.userAgent;
      if (u.indexOf("Android") > -1 && u.indexOf("Linux") > -1) {
        return true;
      }
      return false;
    },
    fullScreen() {
      const player = this.$refs.videoPlayer.player;
      player.requestFullscreen(); //api
      player.isFullscreen(true);
      player.play();
    },
    //
    pause() {
      this.player.pause();
    },
    play() {
      this.player.play();
    },
    currentTime(t) {
      this.player.currentTime(t);
    },
    //
    playerNext() {
      this.$emit("playerNext");
    },
    //
    //  playerList() {
    //    this.$emit("playerList");
    //  },
    //
    playerPlay(player) {
      this.$emit("playerPlay");
    },
    //
    playerPause(player) {
      this.$emit("playerPause");
    },
    //
    playerEnded(player) {
      this.$emit("playerEnded");
    },
    //
    playerStateChanged(state) {
      this.$emit("playerState", state);
    },

    //
    playerReadied(player) {
      if (this.isAndroid()) {
        this.player.muted(true);
      }
    },
    // 
    playerLoadeddata(player) {
      if (this.isAndroid()) {
        if (this.autoPlay && this.player) {
          //
          this.player.play();
        }
        if (this.netWork) {
          this.$emit("netWorkAutoPlayer");
        }
      } else {
        //
        if (this.seekTime) {
          this.player.currentTime(this.seekTime);
          this.playingTime = this.player.currentTime();
        }
        if (this.player && this.netWork) {
          this.player.muted(true);
          setTimeout(() => {
            this.player.play();
            this.$emit("netWorkAutoPlayer");
          }, 200);
        }
      }
    },
    // DOMreadyState
    playerWaiting(player) {
      this.timeOutTimer = setTimeout(function () {
        dsf.layer.message(",");
        clearTimeout(this.timeOutTimer);
      }, 5000);
    },
    //
    playerPlaying(player) {
      this.$emit("playerPlaying", player);
    },
    //
    playerTimeupdate(player) {
      if (this.timeOutTimer) clearTimeout(this.timeOutTimer);
      if (this.initPlay && !this.isSeeking) {
        let seekTime = this.seekTime;
        //
        this.isSeeking = true;
        if (this.isAndroid()) {
          window.setTimeout(() => {
            this.initPlay = false;
            if (seekTime) {
              this.player.currentTime(seekTime);
              this.playingTime = this.player.currentTime();
            }
            this.player.muted(false);
            this.player.play();
          }, 1000);
        } else {
          this.initPlay = false;
        }
      } else {
        if (this.allowFastForward) {
          this.playingTime = player.currentTime();
          this.$emit("playerPlayingTime", this.playingTime);
        } else {
          let newTime = player.currentTime();
          //10
          if (newTime - this.playingTime > 10) {
            window.setTimeout(() => {
              this.player.currentTime(this.playingTime);
            });
          } else {
            this.playingTime = player.currentTime();
            this.$emit("playerPlayingTime", newTime);
          }
        }
      }
    },
    playerCanplay(player) {
      // alert('canplay')
    },
    playerCanplaythrough(player) {
      // this.playingTime = this.player.currentTime();
      this.$emit("playerCanplaythrough");
    },
  },
  beforeDestroy() {
    this.$refs.videoPlayer.dispose();
    if (this.timeOutTimer) clearTimeout(this.timeOutTimer);
  },
  updated() {
    console.log("change:", this.videoSource);
  },
  watch: {
    videoSource(n, o) {
      let vs = this.videoSrc();
      if (!vs || !vs.src || vs.type) return;
      this.playerOptions.sources[0].src = vs.src;
      this.playerOptions.sources[0].type = vs.type;
    },
    playIng(state) {
      if (!state) {
        console.log(state);
        //   this.player.pause();
      }
    },
  },
};
</script>
